@import "../../global.scss";
.Contact{
    background-color: black;
    color: black;
    display: flex;
    align-items: center;
    @include mobile{
      flex-direction: column;
    }
    .left {
      @include mobile{
        scale: 70%;
      }
      flex: 1;
      overflow: hidden;

  
      img {
        height: 100%;
      }
    }
    .right{
      @include mobile{
        height: 500px;
        width: 400px;
        scale: 100%;
        align-items: center;
        bottom: 30px
      }
      flex: 1;
      flex-direction: column;
      h1{
        @include mobile{
          display: none;
        }
        color: coral;
        font-weight: 400;
        transform: rotate(90deg);
        float: right;
        font-size: 50px;
        margin-top: 50px;
        left: 1350px;
        @include mobile{
          left: 0px;
          float: none;
          display: flexbox;
          align-items: center;
        }
      }
      .container{
        @include mobile{
          margin-left: 60px;
          scale: 80%;
          margin-top: -50px;
        }
          width: 70%;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-wrap: wrap;
          .item{
            @include mobile{
            
              margin: 20px 30px;
            }
              width: 300px;
              height: 150px;
              border-radius: 20px;
              margin: 20px 50px;
              display: flex;
              align-items: center;
              justify-content: center;
              position: relative;
              transition: all .5s ease;
              cursor: pointer;
          
          h3 {
            position: absolute;
              font-weight: 300;
              font-size: 40px;
              margin: 20px;
            }
            a{
              z-index: 2;
            img {

              border-radius: 20px;
              width: 300px;
              height: 150px;
              object-fit: cover;
              overflow: hidden;
            }
            }
            &:hover {
              background-color: coral;
              img {
                opacity: 0.1;
                z-index: 0;
              }
            }
          }
      }
    }
}