@import "./global.scss";
.app{
    height: 100vh;
    

    .sections{
        @include mobile{
            overflow-x: hidden;
        }
        width: 100%;
        height: calc(100vh - 70px);
        position: relative;
        top: 70px;
        overflow-y: scroll;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; //for firefox
        &::-webkit-scrollbar{
            display: none;
        }
        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}